#loading {
    position: fixed;
}

#loading .mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}


#loading .loading-msg {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -2em;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    font-weight: lighter;
    font-size: 14px;
}

.loading {
    height: 140px;
    width: 140px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-100%);
        -ms-transform: translateX(-50%) translateY(-100%);
            transform: translateX(-50%) translateY(-100%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}



